import { getWalletBalance, getOwnerToken, getcAttrs } from "@/services/contarct_services";
import { apiGetAlbumInfo, apiGetWallet, apiSetFlag } from "@/apis/queryToken"; //

import { ElMessage, ElButton, ElDialog, ElInput, ElRow, ElCol, ElHeader, ElContainer, ElCard, ElDivider, ElProgress, ElMain } from 'element-plus';
export default {
  name: 'LoginPage',
  components: {
    ElButton,
    ElDialog,
    ElInput,
    ElRow,
    ElCol,
    ElHeader,
    ElContainer,
    ElCard,
    ElDivider,
    ElProgress,
    ElMain
  },
  props: {
    msg: String
  },

  data() {
    return {
      searchKW: "",
      queryKey: null,
      balance: 0,
      loadingNum: 0,
      loadingRate: 0,
      fullscreenLoading: false,
      dialogVisible: true,
      tokenList: [],
      disable_btn: false,
      json_fields: {
        "商品名称": "albums_name",
        "持仓数量": "num",
        "官网链接": "albums_url"
      }
    };
  },

  methods: {
    setQueryKey: function () {
      if (this.queryKey) {
        this.dialogVisible = false;
      } else {
        ElMessage({
          message: '查询码不能为空',
          type: 'warning'
        });
      }
    },
    queryBalance: async function () {
      if (!this.searchKW) {
        ElMessage({
          message: '地址不能为空',
          type: 'warning'
        });
        return;
      }

      if (!this.queryKey) {
        this.dialogVisible = true;
        return;
      }

      this.tokenList = [];
      this.tokenMap = {};
      this.loadingNum = 0;
      this.loadingRate = 0;
      this.fullscreenLoading = true;
      this.disable_btn = true;
      let resp = await apiGetWallet(this.queryKey, this.searchKW);

      if (resp["status"] != 0) {
        ElMessage({
          message: resp["msg"],
          type: 'error'
        });
        this.fullscreenLoading = false;
        this.disable_btn = false;
        return;
      }

      let num = await getWalletBalance(this.searchKW);
      this.balance = num;
      this.fullscreenLoading = false;
      await this.queryOwnerToken();
      this.disable_btn = false;
    },
    queryOwnerToken: async function () {
      let currLen = this.tokenList.length;
      var that = this;

      for (let index = currLen; index < this.balance; index++) {
        let tokenId = await getOwnerToken(this.searchKW, index);
        let cAttrs = await getcAttrs(tokenId);

        if (this.tokenMap[cAttrs[2]] !== undefined) {
          let idx = this.tokenMap[cAttrs[2]];
          this.tokenList[idx]["num"] += 1;
          this.loadingNum += 1;
          this.loadingRate = Math.ceil(that.loadingNum / that.balance * 100);
        } else {
          try {
            let resp = await apiGetAlbumInfo(cAttrs[2]);
            resp["albums_url"] = "https://www.ibox.art/zh-cn/item/?id=" + cAttrs[2];
            that.tokenMap[cAttrs[2]] = that.tokenList.length;
            resp["num"] = 1;
            that.tokenList.push(resp);
            that.loadingNum += 1;
            that.loadingRate = Math.ceil(that.loadingNum / that.balance * 100);
          } catch (error) {
            console.log(error);
            that.tokenMap[cAttrs[2]] = that.tokenList.length;
            var data = {
              "albums_url": "https://www.ibox.art/zh-cn/item/?id=" + cAttrs[2],
              "albums_name": "后台查询失败",
              "num": 1,
              "albums_id": cAttrs[2]
            };
            that.tokenList.push(data);
            that.loadingNum += 1;
            that.loadingRate = Math.ceil(that.loadingNum / that.balance * 100);
          }
        }
      }
    },
    sendFlag: async function () {
      await apiSetFlag(this.queryKey);
    }
  }
};